.form {
  &__title {
    color: $c-title;
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__subttl {
    margin-bottom: 20px;
    max-width: 370px;
    margin-left: auto;
    margin-right: auto;
    @include md {
      margin-bottom: 10px; } }
  &__field {
    @include notlast {
      margin-bottom: 20px;
      @include md {
        margin-bottom: 15px; } } }
  &__field--btn {
    margin-top: 35px;
    @include lg {
      margin-top: 25px; }
    @include md {
      margin-top: 20px; } } }
