.contacts {
  color: $c-title;
  &__list {
    @include flex(flex-start, normal, row, wrap);
    @include md {
      display: block; } }
  &__item {
    // width: 50%
    padding-right: 10px;
    margin-bottom: .5em;
    &:nth-child(odd) {
      margin-right: 60px;
      @include lg {
        margin-right: 40px; }
      @include md {
        margin-right: 0; } } }
  &__link {
    @include tr(color .3s);
    @include hover {
      color: $accent; }
    .icon {
      position: relative;
      top: .1em; } } }
