.article {
  @include clr;
  p, ul, ol, blockquote, img {
    @include notlast {
      margin-bottom: 30px;
      margin-bottom: var(--grid-gap, 30px); } }
  h1, h2, h3, h4, h5, h6, .title {
    font-weight: 700;
    color: $c-title;
    margin-bottom: .3em; }
  h1 {
    &:not(.title) {
      @extend .title;
      @extend .title--h3; } }
  h2 {
    &:not(.title) {
      @extend .title;
      @extend .title--h4; } }
  a {
    text-decoration: underline;
    color: $blue; }
  ul {
    li {
      @include list-bullet;
      &::before {
        margin-right: .5em;
        font-weight: 700; } } }
  ol {
    @include counter('text', ')');
    li {
      &::before {
        margin-right: .5em;
        font-weight: 700; } } }

  img {
    &.alignright, &.alignleft {
      @include md {
        width: 100%;
        margin-left: 0;
        margin-right: 0; } }
    &.alignright {
      float: right;
      margin-left: 30px;
      margin-left: var(--grid-gap, 30px); }
    &.alignleft {
      float: left;
      margin-right: 30px;
      margin-right: var(--grid-gap, 30px); } } }
