.hero {
  $this: &;
  color: $white;
  @include cover;
  background-color: $gray-lightest;
  text-align: center;
  padding-top: 25px;
  padding-bottom: 75px;
  &__breadcrumbs {
    text-align: left;
    margin-bottom: 40px;
    @include md {
      margin-bottom: 20px; } }
  &__title {
    margin-bottom: 20px;
    color: $white;
    @include md {
      margin-bottom: 10px; } }
  &__subttl {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__btns {
    @include flex;
    @include md {
      margin-bottom: 30px; }
    @include xs {
      display: block; } }
  &__btn {
    @include notlast {
      margin-right: 15px;
      @include xs {
        margin-right: 0;
        margin-bottom: 15px; } } }
  &__img {
    display: block;
    @include md {
      margin-left: auto;
      margin-right: auto; } }
  &__date {
    justify-content: center;
    margin-bottom: 10px; }
  &__contacts {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }

  &--lg {
    text-align: left;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    #{$this}__inner {
      padding-top: 8%;
      padding-bottom: 8%;
      @include xs {
        padding-top: 80px;
        padding-bottom: 80px; } } }
  &--md {
    #{$this}__inner {
      padding-top: 7%;
      padding-bottom: 7%;
      @include xs {
        padding-top: 60px;
        padding-bottom: 60px; } } }
  &--section {
    background-position: 100% 50%;
    color: inherit;
    overflow: hidden;
    #{$this}__title {
      color: $c-title; }
    #{$this}__inner {
      padding-bottom: 0;
      padding-top: 30px;
      @include flex(flex-end, normal, column);
      min-height: 70vh;
      @include lg {
        min-height: 0; }
      // +windowHeight($header-height)
      // +lg
 }      //   +windowHeight($header-height-lg)

    #{$this}__btn {
      margin-bottom: 30px; }
    #{$this}__img {
      @include sm {
        margin-bottom: -30px; }
      @include xs {
        display: none; } } } }
