.model-card {
  @include flex(flex-start, normal, column);
  height: calc(100% - 50px);
  margin-bottom: 50px;
  @include lg {
    margin-bottom: 30px;
    height: calc(100% - 30px); }
  @include md {
    margin-bottom: 10px;
    height: calc(100% - 10px); }
  @include sm {
    margin-bottom: 0px;
    height: 100%; }
  @include xs {
    // margin-bottom: 0
    height: auto; }
  &__art {
    margin-bottom: 10px; }
  &__content {
    flex-grow: 1;
    @include flex(flex-start, normal, column); }
  &__img {
    @include cover;
    padding-top: 80%;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__title {
    color: $c-title;
    margin-bottom: 8px; }
  &__text {
    margin-bottom: 20px;
    flex-grow: 1;
    @include md {
      margin-bottom: 10px; } }
  &__price {
    margin-bottom: 15px; } }

