.number-block {
  border: 1px solid $gray-lightest;
  padding: 45px;
  text-align: center;
  height: 100%;
  @include flex(flex-start, normal, column);
  @include lg {
    padding: 30px; }
  @include md {
    padding-left: 20px;
    padding-right: 20px; }
  &__text {
    max-width: 320px;
    flex-grow: 1; }
  &__nmb {
    margin-bottom: 10px; } }

.number {
  font-weight: 700;
  color: $accent;
  &__nmb {
    @include css-lock(21, 45, 320, 1399, font-size);
    line-height: 1.2; } }
@supports (-webkit-text-fill-color: transparent) {
  .number {
    @include accent-grad;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text; } }

